const RightCol = ({ post, isSM, onChange }) => {
    const [value, setValue] = useState("accupunture")
    useEffect(() => {
        onChange && onChange(value)
    }, [value])

    return <Box display={"flex"} flexDirection={"column"} alignItems={"end"} padding={isSM ? "0 32px 0 32px" : "0"}>
        <h3>Manage Item</h3>
        <FormControl fullWidth>
            <InputLabel id="demo-simple-select-label">Age</InputLabel>
            <Select
                labelId="demo-simple-select-label"
                id="demo-simple-select"
                value={value}
                label="Age"
                onChange={(e) => setValue(e.target.value)}
            >
                <MenuItem value={""}>All</MenuItem>
                <MenuItem value={"accupunture"}>Accupunture</MenuItem>
                <MenuItem value={"numberFormula"}>NumberFormula</MenuItem>
                <MenuItem value={"herbalFormula"}>HerbalFormula</MenuItem>
            </Select>
        </FormControl>
        <Divider />
    {
        post.map(e => <a href="">
            {
                e?.data?.title
            }
        </a>)
    }
    </Box >
}


const IndexPage = () => {
    const { isAuth, env } = useAuth()
    const [post, setPosts] = useState([])
    const [preview, setPreview] = useState('')
    const [number, setNumber] = useState('')
    const [day, setDate] = useState('')
    const [month, setMonth] = useState('')
    const [year, setYear] = useState('')
    const [elements, setElements] = useState(null)
    const [accordion, setAccordion] = useState([])
    const theme = useTheme();
    const [numberInfo, setNumberInfo] = useState()
    const isSM = useMediaQuery(theme.breakpoints.up('sm'));
    const [list, setList] = useState();
    const [currentName, setCurrentName] = useState("")

    const refreshData = async (type = ``) => {
        const result = await Parse.Cloud.run('queryItems', {
            type: `post_public-${env}`,
            query: {
                equalTo: ['name', type],
            },
        })
        setPosts(result?.map((e) => e.toJSON()) || [])
    }

    useEffect(async () => {
        refreshData(currentName)
    }, [currentName])

    const handleDelete = async (post) => {
        await Parse.Cloud.run('deleteItems', {
            ids: [post.objectId]
        })
        refreshData(currentName)
    }

    const handlePost = async (post) => {
        console.log(post)
        await Parse.Cloud.run('createItem', {
            type: `post_public-${env}`,
            data: {
                id: post.objectId,
                title: post.title,
                content: post.content,
                ...post,
            },
            name: currentName

        })
        refreshData(currentName)
    }

    return (
        <>
            <ThreeCol env={env} right={<RightCol isSM={isSM} post={post} onChange={(e) => setCurrentName(e)} />}>
                <Seo title="Home" />
                <Box display="flex" justifyContent="center" flexDirection="column" gap="16px">
                    <PostList list={post} isAuth={isAuth()} handleDelete={handleDelete} onPost={handlePost} />
                </Box>
            </ThreeCol>
        </>)
}

export default IndexPage

